* {
  padding: 0;
  margin: 0;
  font-family: "Jost", sans-serif;
  /* font-family: "Quicksand", sans-serif; */
}

body {
  background: #f3f2ef;
  height: 100vh;
  width: 100vw;
}

.container {
  background: #f3f2ef;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 2rem;
}

html,
body {
  font-size: 62.5%;
  height: 100%;
}

html {
  background: #eee;
}

.errMsg {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
}

.box {
  width: 40vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  min-width: 20rem;
  min-height: 35rem;
  /* margin-top:10%; */
}
.inputData {
  /* align-items:center; */
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputFeild {
  align-items: center;
  padding: 15px;
  border: 0px solid black;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  border-radius: 20px;
}
.heading {
  font-size: 3rem;
  /* display: :grid; */
  margin: 4rem;
  text-align: center;
  font-weight: 800;
}

.wave {
  opacity: 0.5;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -25rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.wave.-three {
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.3;
}

.wave.-two {
  animation: drift 7000ms infinite linear;
  opacity: 0.2;
  z-index: 3 !important;
}

.box:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  from {
    transform: rotate(360deg);
  }
}
.info {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 45%;
  z-index: 4;
}

.location {
  margin-top: 1.5rem;
  text-align: center;
  font-weight: 500;
  font-size: 5rem;
  font-weight: 900;
}

.weatherimg {
  font-size: 8rem;
  margin-top: 2rem;
  text-align: center;
  font-weight: 500;
  animation: fas-anime 3s linear infinite alternate;
}

.fa-street-view {
  animation: rotates 3s linear infinite alternate;
}

@keyframes rotates {
  from {
    transform: translateX(-0.5rem);
  }
  to {
    transform: translateX(0.5rem);
  }
}

#date {
  text-align: center;
  margin-top: 0.5rem;
  color: #57606f;
  font-size: 2.2rem;
  font-weight: 300;
  text-transform: uppercase;
}

.temp {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 3.5rem;
  font-weight: 1000;
}

.tempmin_max {
  text-align: center;
  margin-top: 0.3rem;
  font-weight: 1000;
  font-size: 2.2rem;
  color: black;
}

#weathercon {
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
}

#weathercon .fas {
  font-size: 6rem;
  animation: fas-anime 3s linear infinite alternate;
}

@keyframes fas-anime {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.8);
  }
}

@media (max-width: 600px) {
  .box {
    width: 90vw;
    height: 80vh;
  }

  .wave {
    top: 85%;
  }

  #weathercon {
    font-size: 5em;
  }

  .info {
    font-size: 1.5rem;
  }
}
@media (max-height: 500px) {
  .box {
    height: 80vh;
  }

  .wave {
    top: 115%;
  }
}
body > span {
  width: 100vw;
  text-align: center;
  color: grey;
}
